.ReviewsContainer {
    background-color: var(--color-white) !important;
    height: none !important;
    max-width: none !important;
}

.ReviewsTitle {
    padding-top: 10px;
}

.ReviewsTitle h1 {
    margin-top: 16px;
    font-family: "Anton", Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 60px;
    line-height: 78px;
    text-align: center;
    color: var(--color-deepblue);
}

.ReviewsTitle h2 {
    margin-top: 100px;
    text-align: center;
    font-family: "Anton", Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: var(--color-pink);
}

.ReviewsTitle p {
    margin-top: 30px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 28px;
    text-align: center;
    color: var(--color-deepblue);
}

.ReviewsBrandContainer{
    background-color: var(--color-deepblue) !important;
    height: none !important;
    max-width: none !important;
 }

.ReviewsBrand{
    color: var(--color-white);
}

.ReviewsBrand h3 {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: auto;
    line-height: 47px;
    text-align: center;
}

.ReviewsBrand button {
    border-radius: 58px;
    background-color: var(--color-white);
    color: var(--color-deepblue);
    border: none;
    font-weight: 500;
    font-size: 31px;
    line-height: 27px;
    width: 330px;
    height: 100px;
}

.ReviewsBrand button:hover  {
    background-color:var(--color-white) !important;
    color: var(--color-seablue) !important;
}

.ButtonRotate {
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 6px;
    transform: rotate(-90deg);
    color: var(--color-deepblue);
}

.ReviewsBrand button:hover > .ButtonRotate {
    color: var(--color-seablue);
}

